// src/component/TiptapEditor.tsx
import React, {useState} from 'react'
import { useEditor, EditorContent } from '@tiptap/react'
import StarterKit from '@tiptap/starter-kit'
import Image from '@tiptap/extension-image'
import styles from '../styles/TiptapEditor.module.css'
import { uploadImage } from '@/api/blogService/blog'

// ‚úÖ Í≥µÎ∞± Î≥¥Ï°¥ Ï≤òÎ¶¨ Ìï®Ïàò
function preserveSpacesInHTML(html: string): string {
  return html
  // Îπà p ÌÉúÍ∑∏Î•º &nbsp;Í∞Ä ÏûàÎäî p ÌÉúÍ∑∏Î°ú Î≥ÄÌôò
  .replace(/<p><\/p>/g, '<p>&nbsp;</p>')
  .replace(/<p>\s*<\/p>/g, '<p>&nbsp;</p>')
  // Î¨∏Ïû• ÏãúÏûë Í≥µÎ∞±ÏùÑ &nbsp;Î°ú Î≥ÄÌôò
  .replace(/(<p[^>]*>)(\s+)/g, (match, tag, spaces) => {
    return tag + '&nbsp;'.repeat(spaces.length)
  })
  // Ïó∞ÏÜçÎêú 2Í∞ú Ïù¥ÏÉÅÏùò Í≥µÎ∞±ÏùÑ &nbsp;Î°ú Î≥ÄÌôò
  .replace(/(\s{2,})/g, (match) => {
    return '&nbsp;'.repeat(match.length)
  })
}

const MenuBar = ({ editor }: { editor: any }) => {
  if (!editor) return null

  return (
      <div className={styles.menuBar}>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleHeading({ level: 1 }).run()}
            className={editor.isActive('heading', { level: 1 }) ? styles.isActive : ''}
        >
          H1
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleHeading({ level: 2 }).run()}
            className={editor.isActive('heading', { level: 2 }) ? styles.isActive : ''}
        >
          H2
        </button>

        <button
            type="button"
            onClick={() => editor.chain().focus().toggleBold().run()}
            className={editor.isActive('bold') ? styles.isActive : ''}
        >
          B
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleItalic().run()}
            className={editor.isActive('italic') ? styles.isActive : ''}
        >
          I
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleStrike().run()}
            className={editor.isActive('strike') ? styles.isActive : ''}
        >
          S
        </button>

        <button
            type="button"
            onClick={() => editor.chain().focus().toggleBulletList().run()}
            className={editor.isActive('bulletList') ? styles.isActive : ''}
        >
          ‚Ä¢List
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleOrderedList().run()}
            className={editor.isActive('orderedList') ? styles.isActive : ''}
        >
          1.List
        </button>

        <button
            type="button"
            onClick={() => document.getElementById('image-input')?.click()}>
          üñºImg
        </button>
        <input
            id="image-input"
            type="file"
            accept="image/*"
            style={{ display: 'none' }}
            onChange={async (e: any) => {
              const file = e.target.files?.[0]
              if (!file) return

              const formData = new FormData()
              formData.append("file", file)

              try {
                const res = await fetch('/api/upload', {
                  method: "POST",
                  body: formData,
                })
                if(res.ok) {
                  const { url } = await res.json()
                  editor.chain().focus().setImage({ src: url }).run()
                }
              } catch (err) {
                console.error("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïã§Ìå®", err)
              }
            }}
        />

        <button
            type="button"
            onClick={() => editor.chain().focus().toggleCodeBlock().run()}
            className={editor.isActive('codeBlock') ? styles.isActive : ''}
        >
          {'</>'}
        </button>
      </div>
  )
}

interface TiptapProps {
  content?: string
  onChange?: (html: string) => void
  onThumbnailChange?: (url: string | null) => void;
}

const TiptapEditor = ({ content = '', onChange }: TiptapProps) => {
  const [thumbnailUrl, setThumbnailUrl] = useState<string | null>(null); // ÌòÑÏû¨ Ïç∏ÎÑ§Ïùº Ïù¥ÎØ∏ÏßÄ URL
  const editor = useEditor({

    extensions: [
      StarterKit.configure({
        // ‚úÖ paragraphÏóêÏÑú Ïó∞ÏÜç Ï§ÑÎ∞îÍøàÏùÑ ÏúÑÌï¥ preserveWhitespace ÏÑ§Ï†ï
        paragraph: {
          HTMLAttributes: {
            style: 'white-space: pre-wrap;',
          },
        },
        // ‚úÖ hardBreak ÌôúÏÑ±Ìôî - EnterÎ°ú Ï§ÑÎ∞îÍøà (Í∏∞Î≥∏ ÎèôÏûë)
        hardBreak: {
          keepMarks: false,
        },
      }),
      Image,
    ],
    content,
    immediatelyRender: false,


    onUpdate: ({ editor }) => {
      // ‚úÖ HTML Ï∂úÎ†• Ïãú Í≥µÎ∞±ÏùÑ Î≥¥Ï°¥ÌïòÎèÑÎ°ù Ï≤òÎ¶¨
      const html = editor.getHTML()
      const preservedHtml = preserveSpacesInHTML(html)
      onChange?.(preservedHtml)
    },
    // ‚úÖ ÏóêÎîîÌÑ∞ Ï†ÑÏ≤¥ ÏÜçÏÑ± ÏÑ§Ï†ï
    editorProps: {
      attributes: {
        style: 'white-space: pre-wrap;', // Í≥µÎ∞± Î≥¥Ï°¥
      },
    },
    // ‚úÖ Í≥µÎ∞± Î≥¥Ï°¥ ÏÑ§Ï†ï
    parseOptions: {
      preserveWhitespace: 'full',
    },
  })

  return (
      <div className={styles.editorWrapper}>
        <div className={styles.editorPane}>
          <MenuBar editor={editor} />
          <EditorContent editor={editor} className={styles.editorContent} />
        </div>

        <div className={styles.previewPane}>
          <h3 className={styles.previewTitle}>ÎØ∏Î¶¨Î≥¥Í∏∞</h3>
          <div
              className={styles.previewContent}
              style={{ whiteSpace: 'pre-wrap' }}
              dangerouslySetInnerHTML={{
                __html: (editor?.getHTML() || '')
                // ‚úÖ Îπà p ÌÉúÍ∑∏Ïóê Í≥µÎ∞± Î¨∏Ïûê Ï∂îÍ∞Ä
                .replace(/<p><\/p>/g, '<p>&nbsp;</p>')
                .replace(/<p>\s*<\/p>/g, '<p>&nbsp;</p>')
              }}
          />
        </div>
      </div>
  )
}

export default TiptapEditor