export async function uploadImage(file: File): Promise<{ url: string }> {
  const url = `${BLOGSERVICE_API}/upload`;

  const formData = new FormData();
  formData.append("file", file); // Î∞±ÏóîÎìú @RequestParam("file")Í≥º Ïù¥Î¶Ñ ÏùºÏπò

  const response = await fetch(url, {
    method: "POST",
    body: formData, // ÌååÏùº ÏóÖÎ°úÎìú Ïãú Content-TypeÏùÄ ÏÑ§Ï†ïÌïòÏßÄ ÏïäÏäµÎãàÎã§ (Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÏûêÎèôÏúºÎ°ú multipart/form-data ÏÑ§Ï†ï)
  });

  if (response.ok) {
    // Î∞±ÏóîÎìúÏóêÏÑú { "url": "..." } ÌòïÌÉúÏùò JSONÏùÑ Î∞òÌôò
    return await response.json();
  } else {
    const errorText = await response.text();
    throw new Error(errorText || `Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïã§Ìå®: HTTP ${response.status}`);
  }
}

export async function toggleLike(postId: number, userSignId: string): Promise<LikeToggleResponseDTO> {
  const url = `${BLOGSERVICE_API}/likes/${postId}`;

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "userSignId": userSignId, // Ìó§ÎçîÏóê userSignId Ìè¨Ìï®
      },
    });

    if (response.ok) {
      return await response.json(); // { isLiked: true, likeCount: 10 } Î∞òÌôò
    } else {
      const errorText = await response.text();
      throw new Error(errorText || `Ï¢ãÏïÑÏöî Ï≤òÎ¶¨ Ïã§Ìå®: HTTP ${response.status}`);
    }
  } catch (error) {
    console.error("Ï¢ãÏïÑÏöî API Ïò§Î•ò:", error);
    throw error;
  }
}

// ‚úÖ ÏàòÏ†ïÎêú Î∂ÄÎ∂Ñ: Î∞òÌôò ÌÉÄÏûÖÏùÑ PostEntityÎ°ú Î™ÖÌôïÌûà ÌïòÍ≥†, response.json()ÏùÑ ÏÇ¨Ïö©Ìï©ÎãàÎã§.
export async function writeFeed(formData: FormData, userSignId: string): Promise<PostEntity> { // <--- Î∞òÌôò ÌÉÄÏûÖ Î™ÖÏãú
  console.log("Í∏ÄÏì∞Í∏∞ Î©îÏÑúÎìú Ïã§Ìñâ")
  const url = `${BLOGSERVICE_API}/write`;

  const tagsValue = (formData.get("tags") as string | null) || '';

  const requestBody: PostCreationRequestDTO = {
    title: formData.get("title") as string,
    content: formData.get("content") as string,
    isPublished: formData.get("isPublished") === 'true',
    thumbnail: formData.get("thumbnail") as string | undefined,
    // ÏâºÌëú(,)Î°ú Íµ¨Î∂ÑÎêú ÌÉúÍ∑∏ Î¨∏ÏûêÏó¥ÏùÑ Î∞∞Ïó¥Î°ú Î≥ÄÌôò
    tags: tagsValue
    .split(',')
    .map(tag => tag.trim())
    .filter(tag => tag.length > 0),
  };

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "userSignId": userSignId,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(requestBody),
    });

    if (response.ok) {
      // üö® ÏàòÏ†ï: response.text() ÎåÄÏã† response.json()ÏùÑ ÏÇ¨Ïö©Ìï©ÎãàÎã§.
      const result: PostEntity = await response.json();
      console.log("Í≤åÏãúÍ∏Ä ÏûëÏÑ± ÏÑ±Í≥µ:", result);
      return result; // PostEntity Í∞ùÏ≤¥Î•º Î∞òÌôò
    } else {
      const errorText = await response.text();
      console.error(`Í≤åÏãúÍ∏Ä ÏûëÏÑ± Ïã§Ìå® (HTTP ${response.status}):`, errorText);
      throw new Error(errorText || `Í≤åÏãúÍ∏Ä ÏûëÏÑ± Ïã§Ìå®: ÏÉÅÌÉú ÏΩîÎìú ${response.status}`);
    }

  } catch (error) {
    console.error("API Ìò∏Ï∂ú Ï§ë ÏòàÏô∏ Î∞úÏÉù:", error);
    throw error;
  }
}

export async function updatePost(postId: number, userSignId: string ,postData: PostCreationRequestDTO): Promise<PostEntity> {
  // Î∞±ÏóîÎìú Ïª®Ìä∏Î°§Îü¨: @PostMapping("/write/{postId}")
  const url = `${BLOGSERVICE_API}/write/${postId}`;

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "userSignId": userSignId,
      },
      body: JSON.stringify(postData),
    });

    if (response.ok) {
      return await response.json();
    } else {
      const errorText = await response.text();
      throw new Error(errorText || `Í≤åÏãúÍ∏Ä ÏàòÏ†ï Ïã§Ìå®: HTTP ${response.status}`);
    }
  } catch (error) {
    console.error('Í≤åÏãúÍ∏Ä ÏàòÏ†ï Ï§ë Ïò§Î•ò Î∞úÏÉù:', error);
    throw error;
  }
}

