export async function readPost(postId: number): Promise<PostEntity> {
  console.log("Í≤åÏãúÎ¨º ÏùΩÏñ¥Ïò§Í∏∞ Ïã§Ìñâ");
  const url = `${BLOGSERVICE_API}/readpost?postId=${postId}`;

  const response = await fetch(url, {
    method: "GET",
  });

  if (response.ok) {
    // 1. ÏÑ±Í≥µ Ïãú: ÏòàÏÉÅÎåÄÎ°ú PostEntity JSON Í∞ùÏ≤¥Î•º Î∞òÌôòÌï©ÎãàÎã§.
    return await response.json();
  } else {
    // 2. Ïã§Ìå® Ïãú: response.text()Î•º ÏÇ¨Ïö©ÌïòÎêò, ÏùëÎãµ Î≥∏Î¨∏Ïù¥ ÎπÑÏñ¥ÏûàÏùÑ Í≤ΩÏö∞Î•º ÎåÄÎπÑÌï©ÎãàÎã§.
    const errorText = await response.text().catch(() => ''); // ÌÖçÏä§Ìä∏ ÌååÏã± Ïã§Ìå® Ïãú Îπà Î¨∏ÏûêÏó¥ Î∞òÌôò

    // ÏóêÎü¨ Î©îÏãúÏßÄ Íµ¨ÏÑ±: ÏÑúÎ≤ÑÍ∞Ä Î≥¥ÎÇ∏ Î©îÏãúÏßÄÍ∞Ä ÏûàÏúºÎ©¥ ÏÇ¨Ïö©, ÏóÜÏúºÎ©¥ Í∏∞Î≥∏ Î©îÏãúÏßÄ ÏÇ¨Ïö©
    const errorMessage = errorText || `Í≤åÏãúÎ¨º Ï°∞Ìöå Ïã§Ìå®: HTTP ${response.status} ÏùëÎãµ`;

    throw new Error(errorMessage);
  }
}
