import { USERSERVICE_API } from "@/config/env";
import {
  UserDTO,
  LoginDTO,
  FriendReqDTO,
  FriendsResDTO,
} from "@/types/userService/user";

// Î°úÍ∑∏Ïù∏ ÏùëÎãµ ÌÉÄÏûÖ Ï†ïÏùò (accessTokenÏùÄ Ìó§ÎçîÏóêÏÑú Ï∂îÏ∂ú)
export interface LoginResponse {
  userSignId: string;
  role: string;
  refreshToken: string;
  accessToken: string; // HTTP Ìó§ÎçîÏóêÏÑú Ï∂îÏ∂úÎê®
}

// 1. ÌöåÏõêÍ∞ÄÏûÖ (POST /user/signup)
export async function signup(userDto: UserDTO) {
  const url = `${USERSERVICE_API}/signup`;

  const response = await fetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(userDto),
  });

  const text = await response.text();

  if (response.status === 201) {
    return { success: true, message: text || "ÌöåÏõêÍ∞ÄÏûÖÏù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§." };
  }

  // Î™®Îì† ÏóêÎü¨ÎèÑ Î©îÏãúÏßÄ Í∑∏ÎåÄÎ°ú Î∞òÌôò
  return { success: false, message: text || "ÌöåÏõêÍ∞ÄÏûÖ Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§." };
}

// 2. Î°úÍ∑∏Ïù∏ (POST /user/login)
export async function login(loginDto: LoginDTO): Promise<LoginResponse> {
  const url = `${USERSERVICE_API}/login`;

  const response = await fetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(loginDto),
  });

  if (response.ok) { // 200 OK
    // Authorization Ìó§ÎçîÏóêÏÑú Bearer ÌÜ†ÌÅ∞ Ï∂îÏ∂ú
    const authHeader = response.headers.get("Authorization");
    const accessToken = authHeader?.startsWith("Bearer ") ? authHeader.substring(7) : "";

    // ÏùëÎãµ Î≥∏Î¨∏ ÌååÏã± (Map<String, String> -> JS Object)
    const body: { [key: string]: string } = await response.json();

    return {
      userSignId: body["userSignId"],
      role: body["role"],
      refreshToken: body["refreshToken"],
      accessToken: accessToken,
    };
  } else {
    throw new Error(`Î°úÍ∑∏Ïù∏ Ïã§Ìå®: HTTP ${response.status}`);
  }
}

// 3. ÏπúÍµ¨ ÏöîÏ≤≠ (POST /user/friends)
export async function requestFriend(requesterSignId: string, reqDto: FriendReqDTO): Promise<FriendsResDTO> {
  const url = `${USERSERVICE_API}/friends`;

  const response = await fetch(url, {
    method: "POST",
    headers: {
      "userSignId": requesterSignId, // ÏöîÏ≤≠Ïûê ID (RequestHeader)
      "Content-Type": "application/json",
    },
    body: JSON.stringify(reqDto),
  });

  if (response.status === 201) { // 201 CREATED
    return await response.json(); // FriendsResDTO Î∞òÌôò
  } else {
    // ÏÑúÎ≤ÑÏùò ÏÉÅÌÉú ÏΩîÎìú Î∞è ÏòàÏô∏ Ï≤òÎ¶¨ Î°úÏßÅ Î∞òÏòÅ
    let errorMessage = `ÏπúÍµ¨ ÏöîÏ≤≠ Ïã§Ìå®: HTTP ${response.status}`;

    switch (response.status) {
      case 404: // IllegalArgumentException (IDÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏùå)
        errorMessage = "ÏöîÏ≤≠Ïûê ÎòêÎäî ÏàòÏã†Ïûê IDÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.";
        break;
      case 400: // IllegalStateException (Î≥∏Ïù∏ÏóêÍ≤å Ïã†Ï≤≠)
        errorMessage = "Î≥∏Ïù∏ÏóêÍ≤å ÏπúÍµ¨ Ïã†Ï≤≠ÏùÑ Ìï† Ïàò ÏóÜÏäµÎãàÎã§.";
        break;
      case 409: // IllegalStateException (Ïù¥ÎØ∏ ÏπúÍµ¨/ÏöîÏ≤≠ Ï§ë)
        errorMessage = "Ïù¥ÎØ∏ ÏπúÍµ¨ Í¥ÄÍ≥ÑÏù¥Í±∞ÎÇò ÏöîÏ≤≠Ïù¥ Ï†ÑÏÜ°Îêú ÏÉÅÌÉúÏûÖÎãàÎã§.";
        break;
      case 500: // Í∑∏ Ïô∏ ÏÑúÎ≤Ñ Ïò§Î•ò
        errorMessage = "ÏπúÍµ¨ ÏöîÏ≤≠ Ï§ë ÏÑúÎ≤Ñ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.";
        break;
    }

    throw new Error(errorMessage);
  }
}

// 4. ÏπúÍµ¨ ÏöîÏ≤≠ ÏàòÎùΩ (PUT /user/friends/{requesterSignId}/accept)
export async function acceptFriend(receiverSignId: string, requesterSignId: string): Promise<FriendsResDTO> {
  // requesterSignIdÎäî PathVariableÎ°ú URLÏóê Ìè¨Ìï®
  const url = `${USERSERVICE_API}/friends/${requesterSignId}/accept`;

  const response = await fetch(url, {
    method: "PUT",
    headers: {
      "userSignId": receiverSignId, // ÏàòÎùΩÌïòÎäî ÏÇ¨Ïö©Ïûê ID (RequestHeader)
      "Content-Length": "0", // bodyÍ∞Ä ÏóÜÎäî PUT ÏöîÏ≤≠
    },
  });

  if (response.ok) { // 200 OK
    return await response.json(); // FriendsResDTO Î∞òÌôò
  } else {
    // ÏÑúÎ≤ÑÏùò ÏòàÏô∏ Ï≤òÎ¶¨ Î°úÏßÅ Î∞òÏòÅ
    let errorMessage = `ÏπúÍµ¨ ÏöîÏ≤≠ ÏàòÎùΩ Ïã§Ìå®: HTTP ${response.status}`;

    switch (response.status) {
      case 404: // IllegalArgumentException (ÏöîÏ≤≠ÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏùå)
        errorMessage = "ÏöîÏ≤≠ÏùÑ Î≥¥ÎÇ∏ ÏÇ¨Ïö©Ïûê IDÎ•º Ï∞æÏùÑ Ïàò ÏóÜÍ±∞ÎÇò, ÎåÄÍ∏∞ Ï§ëÏù∏ ÏöîÏ≤≠Ïù¥ ÏóÜÏäµÎãàÎã§.";
        break;
      case 400: // IllegalStateException (ÏûêÍ∏∞ ÏûêÏã†ÏóêÍ≤å ÏöîÏ≤≠)
        errorMessage = "ÏûêÍ∏∞ ÏûêÏã†Ïùò ÏöîÏ≤≠ÏùÑ ÏàòÎùΩÌï† Ïàò ÏóÜÏäµÎãàÎã§.";
        break;
      case 409: // IllegalStateException (Ïù¥ÎØ∏ ÏπúÍµ¨/PENDING ÏÉÅÌÉú ÏïÑÎãò)
        errorMessage = "Ïù¥ÎØ∏ ÏπúÍµ¨ Í¥ÄÍ≥ÑÏù¥Í±∞ÎÇò ÏöîÏ≤≠ ÏÉÅÌÉúÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.";
        break;
      case 500: // Í∑∏ Ïô∏ ÏÑúÎ≤Ñ Ïò§Î•ò
        errorMessage = "ÏπúÍµ¨ ÏöîÏ≤≠ ÏàòÎùΩ Ï§ë ÏÑúÎ≤Ñ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.";
        break;
    }

    throw new Error(errorMessage);
  }
}