// src/component/blogService/WritePage.tsx

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/router'; // Pages RouterÏö© ÌõÖ
import TiptapEditor from '../../component/TiptapEditor'; // ÏóêÎîîÌÑ∞ Ïª¥Ìè¨ÎÑåÌä∏
import { writeFeed, readPost, updatePost } from '../../api/blogService/blog'; // API Ìï®Ïàò
import { PostCreationRequestDTO } from '../../types/blogService/blogType'; // ÌÉÄÏûÖ Ï†ïÏùò
import styles from '../../styles/blogService/write.module.css'; // CSS Î™®Îìà

// Props ÌÉÄÏûÖ Ï†ïÏùò
interface WritePageProps {
  postId?: number; // ÏàòÏ†ï Î™®ÎìúÏùº ÎïåÎßå Ï°¥Ïû¨
}

const WritePage: React.FC<WritePageProps> = ({ postId }) => {
  const router = useRouter();

  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [tags, setTags] = useState<string[]>([]); // ÌÉúÍ∑∏ Î∞∞Ïó¥
  const [tagInput, setTagInput] = useState('');  // ÌÉúÍ∑∏ ÏûÖÎ†• ÌïÑÎìú
  const [thumbnail, setThumbnail] = useState('');
  const [isPublished, setIsPublished] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isInitialLoad, setIsInitialLoad] = useState(false); // Ï¥àÍ∏∞ Îç∞Ïù¥ÌÑ∞ Î°úÎìú ÌîåÎûòÍ∑∏

  // 3. [ÏàòÏ†ï Î™®Îìú] Îç∞Ïù¥ÌÑ∞ Î°úÎìú Î°úÏßÅ
  useEffect(() => {
    if (postId && !isInitialLoad) {
      const loadPostData = async () => {
        try {
          setIsLoading(true);
          const data = await readPost(postId);
          setTitle(data.title);
          setContent(data.content);
          setTags(data.tags || []);
          setThumbnail(data.thumbnail || '');
        } catch (error) {
          console.error("Îç∞Ïù¥ÌÑ∞ Î°úÎìú Ïã§Ìå®:", error);
          alert("Í≤åÏãúÍ∏Ä Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
          router.back();
        } finally {
          setIsLoading(false);
          setIsInitialLoad(true);
        }
      };
      loadPostData();
    }
  }, [postId, isInitialLoad, router]);


  // ÌÉúÍ∑∏ ÏûÖÎ†• Ï≤òÎ¶¨ (Enter ÌÇ§Î°ú ÌÉúÍ∑∏ Ï∂îÍ∞Ä)
  const handleTagKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' && tagInput.trim()) {
      e.preventDefault();
      const newTag = tagInput.trim();
      if (!tags.includes(newTag)) {
        setTags([...tags, newTag]);
      }
      setTagInput('');
    }
  };

  const removeTag = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };


  // 4. [Ï†ÄÏû•/ÏàòÏ†ï] Ìï∏Îì§Îü¨ Î°úÏßÅ Î∂ÑÍ∏∞
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!title.trim() || !content.trim()) {
      alert("Ï†úÎ™©Í≥º ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    const userSignId = localStorage.getItem('userSignId');
    if (!userSignId) {
      alert("Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Í∞Ä ÏóÜÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    setIsLoading(true);

    try {
      if (postId) {
        // üÖ∞Ô∏è ÏàòÏ†ï Î™®Îìú (Update)
        const updateData: PostCreationRequestDTO = {
          title,
          content,
          tags,
          isPublished,
          thumbnail: thumbnail || undefined,
        };

        // blog.tsÏóê Ï†ïÏùòÎêú updatePost(postId, userSignId, postData) Ìò∏Ï∂ú
        await updatePost(postId, userSignId, updateData);

        alert("Í≤åÏãúÍ∏ÄÏù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.");
        router.push(`/post/${postId}`); // ÏàòÏ†ï ÌõÑ ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô

      } else {
        // üÖ±Ô∏è ÏûëÏÑ± Î™®Îìú (Create)
        const formData = new FormData();
        formData.append('title', title);
        formData.append('content', content);
        formData.append('isPublished', String(isPublished));
        if (thumbnail) formData.append('thumbnail', thumbnail);
        formData.append('tags', tags.join(',')); // Î∞∞Ïó¥ÏùÑ ÏâºÌëú Î¨∏ÏûêÏó¥Î°ú Î≥ÄÌôò

        // üö® ÏàòÏ†ï: writeFeedÎ•º Ìïú Î≤àÎßå Ìò∏Ï∂úÌïòÍ≥†, PostEntity Í∞ùÏ≤¥Î•º Î∞òÌôòÎ∞õÏäµÎãàÎã§. (JSON.parse Î∂àÌïÑÏöî)
        const result = await writeFeed(formData, userSignId);

        // üö® ÏàòÏ†ï: PostEntity Í∞ùÏ≤¥ÏóêÏÑú postIdÎ•º ÏßÅÏ†ë ÏÇ¨Ïö©Ìï©ÎãàÎã§.
        const newPostId = result.postId;

        if (newPostId) {
          alert("Í∏ÄÏù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Îì±Î°ùÎêòÏóàÏäµÎãàÎã§!");
          router.push(`/post/${newPostId}`);
        } else {
          // postIdÍ∞Ä ÏóÜÎäî Í≤ΩÏö∞Î•º ÏúÑÌïú ÏïàÏ†Ñ Ïû•Ïπò
          alert("Í∏ÄÏù¥ Îì±Î°ùÎêòÏóàÏúºÎÇò IDÎ•º Î∞õÏßÄ Î™ªÌñàÏäµÎãàÎã§. ÌôàÏúºÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§.");
          router.push('/');
        }
      }
    } catch (error) {
      console.error("ÏûëÏóÖ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      alert("ÏûëÏóÖ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading && !isInitialLoad) {
    return (
        <div className={styles.writePageContainer}>
          <p>Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...</p>
        </div>
    );
  }

  return (
      <div className={styles.writePageContainer}>
        <div className={styles.writeHeader}>
          <h1 className={styles.writeTitle}>{postId ? "Í≤åÏãúÍ∏Ä ÏàòÏ†ï" : "ÏàúÍ∞ÑÍ≥º ÏàúÍ∞ÑÏù¥ Î™®Ïó¨ ÏÇ∂ÏùÑ Ïù¥Î£®Î©∞"}</h1>
        </div>

        <form onSubmit={handleSubmit} className={styles.form}>
          <div>
            <label className={styles.label}>Ï†úÎ™©</label>
            <input
                type="text"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder="Ïò§ÎäòÏùò Ïù¥ÏïºÍ∏∞Î•º Îì§Î†§Ï£ºÏÑ∏Ïöî"
                className={styles.titleInput}
                required
            />
          </div>

          <div>
            <label className={styles.label}>Ïç∏ÎÑ§Ïùº URL (ÏÑ†ÌÉù)</label>
            <input
                type="text"
                value={thumbnail}
                onChange={(e) => setThumbnail(e.target.value)}
                placeholder="https://example.com/thumbnail.jpg"
                className={styles.textInput}
            />
          </div>

          <div>
            <label className={styles.label}>ÎÇ¥Ïö©</label>
            <TiptapEditor content={content} onChange={setContent}/>
          </div>

          <div
              className={styles.tagInputGroup}
              style={!styles.tagInputGroup ? { marginBottom: '20px' } : undefined}
          >
            <label className={styles.label}>ÌÉúÍ∑∏</label>

            {/* ÌÉúÍ∑∏ Î™©Î°ù ÌëúÏãú */}
            <div
                className={styles.tagList}
                style={!styles.tagList ? { display: 'flex', flexWrap: 'wrap', gap: '8px', marginBottom: '8px' } : undefined}
            >
              {tags.map(tag => (
                  <span
                      key={tag}
                      className={styles.tagItem}
                      style={!styles.tagItem ? { background: '#f1f3f5', padding: '4px 8px', borderRadius: '4px', display: 'flex', alignItems: 'center' } : undefined}
                  >
                #{tag}
                    <button
                        type="button"
                        onClick={() => removeTag(tag)}
                        className={styles.removeTagBtn}
                        style={!styles.removeTagBtn ? { border: 'none', background: 'none', cursor: 'pointer', marginLeft: '4px' } : undefined}
                    >
                    √ó
                </button>
              </span>
              ))}
            </div>

            {/* ÌÉúÍ∑∏ ÏûÖÎ†• ÌïÑÎìú */}
            <input
                type="text"
                value={tagInput}
                onChange={(e) => setTagInput(e.target.value)}
                onKeyDown={handleTagKeyDown}
                placeholder="ÌÉúÍ∑∏Î•º ÏûÖÎ†•ÌïòÍ≥† Enter"
                className={styles.textInput}
            />
            <small className={styles.hint}>ÌÉúÍ∑∏Î•º ÏûÖÎ†•ÌïòÍ≥† ÏóîÌÑ∞Î•º ÎàÑÎ•¥Î©¥ Ï∂îÍ∞ÄÎê©ÎãàÎã§.</small>
          </div>
          <div className={styles.checkboxWrapper}>
            <input
                type="checkbox"
                id="isPublished"
                checked={isPublished}
                onChange={(e) => setIsPublished(e.target.checked)}
                className={styles.checkbox}
            />
            <label htmlFor="isPublished" className={styles.checkboxLabel}>
              {postId ? "ÏàòÏ†ï ÌõÑ Î∞îÎ°ú Í≤åÏãúÌïòÍ∏∞" : "Î∞îÎ°ú Í≤åÏãúÌïòÍ∏∞"}
            </label>
          </div>

          <button
              type="submit"
              disabled={isLoading}
              className={styles.submitButton}
          >
            {isLoading ? "Ï†ÄÏû• Ï§ë..." : postId ? "ÏàòÏ†ï ÏôÑÎ£å" : "ÏûëÏÑ± ÏôÑÎ£åÌïòÍ≥† Í≤åÏãúÌïòÍ∏∞"}
          </button>
        </form>
      </div>
  );
};

export default WritePage;